.btn {
  text-transform: capitalize;
  font-family: 'Rubik', sans-serif;
  display: inline-block;
  font-size: 13px;
  border-radius: 5px;
  font-weight: 500;
  letter-spacing: 1px;
  line-height: 0;
  margin-bottom: 0;
  margin: 10px;
  cursor: pointer;
  transition: color 0.4s linear;
  position: relative;
  z-index: 1;
  border: 0;
  overflow: hidden;
  margin: 0;
  transition: ease-out 0.3s;
}

/* button animation */
.btn:hover {
  color: #fff;
}

.btn:before {
  transition: 0.5s all ease;
  position: absolute;
  top: 0;
  left: 50%;
  right: 50%;
  bottom: 0;
  opacity: 0;
  content: '';
  z-index: -1;
}
.animation_btn_white_to_pink:before {
  background-color: #ec5b53;
}

.animation_btn_pink_to_red:before {
  background-color: #e43239;
}

.btn:hover:before {
  transition: 0.5s all ease;
  left: 0;
  right: 0;
  opacity: 1;
  z-index: -1;
}

.animation_btn_white_to_pink:hover:before {
  background-color: #ec5b53;
}
.animation_btn_pink_to_red:hover:before {
  background-color: #e43239;
}

